import {getToken,setToken,removeToken} from '@/utils/auth.js'
const user={
    state:{ //state
      // token:getToken('token');
      token:null
                  },
    getters:{    //
      listenToken:function(state){
         
        // console.log(state.token)
      }
    },
  actions:{  //
      changeData:function(context){
           context.commit('jian')
      }
    },
    
    mutations:{   // mutationsstate
      jia(state){
        state.count++
      },
      jian(state){
        state.count--
      },
      chuanzhi(state,data){   //mutaionsdataMutation
           state.token=data
      }
    }
  }

  export default user


